@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
* {
  margin: 0%;
  padding: 0%;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #e6e6e6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 2rem;
  padding-bottom: 4rem;
  background-color: transparent;
}
input,
textarea,
select {
  padding: 8px 10px;
  border: 1px solid #c0c0c0;
  border-radius: 4px;
  outline: none;
}
input:focus,
textarea:focus,
select:focus {
  border: 1px solid rgba(81, 203, 238, 1);
  box-shadow: 0 0 5px rgba(81, 203, 238, 1);
}
.form-title > h1 {
  color: #575757;
}
.form-title > span {
  color: #464c4d;
}
.form > form {
  margin-top: 2rem;
  background-color: rgb(255, 255, 255);
  padding: 2rem;
  border-radius: 10px;
}
form > .name,
.address,
.mobile-no,
.email,
.reg_no,
.gender,
.patient-history,
.symptoms,
.paymant-option {
  padding: 1rem 0px;
  display: flex;
}
.name > label {
  color: #575757;
  font-weight: 600;
  margin-right: 1rem;
}
.fname,
.mname,
.lname {
  display: flex;
  flex-direction: column;
  padding: 0px 5px;
}
.fname > input,
.mname > input,
.lname > input {
  font-size: 14px;
  padding: 5px 10px;
}
.fname > span,
.mname > span,
.lname > span {
  color: #464c4d;
  padding: 4px 0px;
  font-size: 13px;
}
.address {
  flex-direction: column;
}
.address > label {
  color: #575757;
  font-weight: 600;
  margin-right: 1rem;
  margin-bottom: 5px;
  padding: 6px 0px;
}
.street-address {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}
.street-address > input {
  width: 70%;
}
.street-address > span {
  padding: 4px 0px;
  font-size: 13px;
  color: #575757;
}
.city-state {
  margin-bottom: 10px;
}
.city-state,
.city,
.state {
  display: flex;
}
.city,
.state {
  padding-right: 10px;
  flex-direction: column;
  width: 34.5%;
}
.city > span,
.state > span {
  padding: 4px 0px;
  font-size: 13px;
  color: #575757;
}

.zip-code {
  display: flex;
  flex-direction: column;
  width: 34.5%;
}
.zip-code > span {
  padding: 4px 0px;
  font-size: 13px;
  color: #575757;
}

.mobile-no,
.email,
.reg_no {
  display: flex;
  flex-direction: column;
  width: 34.5%;
}

.mobile-no > label,
.email > label {
  font-weight: 600;
  margin-right: 1rem;
  margin-bottom: 5px;
  padding: 6px 0px;
  color: #575757;
}

.mobile-no > span,
.email > span {
  padding: 4px 0px;
  font-size: 13px;
  color: #575757;
}

.gender {
  display: flex;
  align-items: center;
}
.gender > label {
  font-weight: 600;
  margin-right: 1rem;
  margin-bottom: 5px;
  padding: 6px 0px;
  color: #575757;
}
.gender > input {
  margin: 5px 10px;
}

.patient-history,
.symptoms {
  display: flex;
  flex-direction: column;
}

.patient-history > lebal,
.symptoms > lebal {
  font-weight: 600;
  margin-right: 1rem;
  margin-bottom: 5px;
  padding: 6px 0px;
  color: #575757;
}

.patient-history > textarea,
.symptoms > textarea {
  resize: none;
  padding: 10px;
  width: 70%;
}
.department,
.doctors,
.appointment_date {
  display: flex;
  flex-direction: column;
  width: 50%;
  color: #575757;
  margin: 20px 0px;
}
.appoitment_label {
  font-weight: 600;
  margin-right: 1rem;
  margin-bottom: 5px;
  padding: 6px 0px;
  color: #575757;
}
.patientType {
  display: flex;
  flex-direction: column;
  width: 30%;
  color: #575757;
  margin: 20px 0px;
}
.department > label,
.doctors > label,
.patientType > label,
.appointment_date > label {
  font-weight: 600;
  margin-right: 1rem;
  margin-bottom: 5px;
  padding: 6px 0px;
  color: #575757;
}
select,
select > option {
  font-size: 14px;
}
.submit {
  margin-top: 2rem;
  padding: 10px 40px;
  cursor: pointer;
  outline: none;
  text-transform: uppercase;
  border-radius: 30px;
  color: #000;
  transition: all 0.4s;
  border: 1px solid black;
  margin-bottom: 1rem;
}
.submit:hover {
  color: #ffff;
  background-color: #00b688;
}
.reg_no > label {
  font-weight: 600;
  margin-right: 1rem;
  margin-bottom: 5px;
  padding: 6px 0px;
  color: #575757;
}
.paymant-option {
  display: flex;
  flex-direction: column;
}
.paymant-option > h3 {
  margin-bottom: 10px;
  color: #575757;
}
.paymant-option > label {
  font-weight: 600;
  margin-right: 1rem;
  margin-bottom: 5px;
  padding: 6px 0px;
  color: #575757;
}
.pay-method {
  padding-bottom: 10px;
  display: flex;
  align-items: center;
}
.pay-method > lebal {
  padding: 5px 10px;
}

.pay-method > select {
  padding: 5px;
}
.default {
  border-bottom: 2px solid black;
}

@media screen and (max-width: 720px) {
  .form > form {
    width: 90%;
    background-color: rgb(255, 255, 255);
    padding-left: 10px;
    padding-top: 0px;
    padding-bottom: 0px;
    padding-right: 0px;
    border-radius: 5px;
  }

  form > .name {
    flex-direction: column;
  }

  .form-title > h1 {
    padding-left: 1rem;
  }
  .form-title > span {
    padding-left: 1rem;
  }
}

.header {
  padding: 1.5rem 1rem;
  display: flex;
  justify-content: space-between;
  background-color: rgb(255, 255, 255);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.header > h1 {
  font-family: "Fjalla One", sans-serif;
  font-size: 2.5rem;
  color: #00b688;
}
.navbar {
  width: 80%;
}
.navlinks {
  list-style: none;
  display: flex;
  float: right;
}
.navlinks > li {
  margin-bottom: 10px;
}
li > a {
  text-decoration: none;
  font-size: 1.5rem;
  color: #000;
  font-weight: 450;
}
.nav-items {
  padding: 10px 15px;
  display: inline-block;
  text-decoration: none;
}
.nav-items::after {
  content: "";
  display: block;
  width: 0;
  height: 3px;
  background: #000;
  transition: width 0.3s;
}

.nav-items:hover::after {
  width: 100%;
}

@media screen and (max-width: 720px) {
  .header {
    flex-direction: column;
  }
  .navbar {
    display: none;
  }
  li > a {
    font-size: 14px;
  }
}

.dr-cards {
  display: flex;
  justify-content: center;
  align-items: center;
}
.Doctors-container {
  height: 80vh;
  background-color: transparent;
}
.Doctors-container > h1 {
  text-align: center;
  text-transform: uppercase;
  color: #424242;
  font-weight: 500;
  padding: 2rem 0px;
  margin-bottom: 2rem;
}

.dr-card {
  padding: 20px 0px;
  margin: 0px 2%;
  text-align: center;
  background-color: #ffff;
  margin-bottom: 1rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.dr-card > h4 {
  font-weight: 500;
  font-size: 20px;
  text-transform: uppercase;
  color: rgb(0, 113, 158);
  margin-bottom: 7px;
  margin-top: 5px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
}

.dr-card > p {
  font-size: 300;
  font-size: 17px;
  margin-bottom: 15px;
}
.appoint-btn {
  text-decoration: none;
  background-color: #00b688;
  color: #ffff;
  padding: 5px 10px;
  border-radius: 5px;
}
.appoint-btn:hover {
  color: #000;
}

@media screen and (max-width: 800px) {
  .dr-cards {
    flex-direction: column;
  }
}

.login-container {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  background-color: #ffff;
  max-width: 50%;
  margin-left: auto;
  margin-right: auto;
  border-radius: 5px;
}
.login-container > h1 {
  margin-top: 2rem;
  text-align: center;
  font-size: 2rem;
}

.log_email,
.password {
  display: flex;
  flex-direction: column;
  width: 30%;
}

.log_email > label,
.password > label {
  font-size: 1.5rem;
  font-weight: 600;
  padding: 10px 0px;
  color: #575757;
}

.log_email > input,
.password > input {
  width: 100%;
}

.btn_login {
  margin-top: 2rem;
  padding: 10px 40px;
  cursor: pointer;
  outline: none;
  text-transform: uppercase;
  border-radius: 30px;
  color: #000;
  transition: all 0.4s;
  border: 1px solid black;
}

.btn_login:hover {
  color: #ffff;
  border: 1px solid #ffff;
  background-color: #00b688;
}

@media screen and (max-width: 800px) {
  .login-container > h1 {
    font-size: 1.5rem;
  }
  .login-container {
    max-width: 90%;
  }
}

.Dr_portal {
  display: flex;
}
.sidebar {
  display: flex;
  flex-direction: column;
  flex: 0.2 1;
  overflow: auto;
  background-color: #30373e;
  height: 85vh;
}
.profile {
  display: flex;
  align-items: center;
}
.profile_info {
  display: flex;
  flex-direction: column;
  padding: 10px 10px;
  color: #ffff;
}
.name {
  font-size: 20px;
  font-weight: 600;
}
.logout_btn {
  padding: 10px 0px;
}
.logout_btn:hover {
  color: rgb(92, 92, 92);
}

.navigation {
  display: flex;
  flex-direction: column;
}
.nav_head {
  background-color: #363e46;
  color: rgb(219, 219, 219);
  padding: 5px 15px;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.MuiSvgIcon-root {
  cursor: pointer;
}
.dropdown,
.dropup {
  opacity: 0;
}
.nav-manu {
  display: flex;
  flex-direction: column;
  background-color: #30373e;
  transition: max-height 0.8s ease-in-out;
}
.nav-item {
  display: flex;
  align-items: center;
  padding: 15px 10px;
  font-size: 14px;
  font-weight: 600;
  color: rgba(219, 219, 219, 0.836);
  cursor: pointer;
}
.nav-item:hover {
  background-color: #262c31;
}
.active {
  color: #43c0b3;
}
.nav-item > span {
  padding-left: 10px;
}

.details {
  display: flex;
  flex-direction: column;
  flex: 0.8 1;
  padding: 20px;
  background-color: #dddddd;
}
.appointment,
.doctors-details,
.patients-details,
.payment-details {
  padding-bottom: 3rem;
}
.dashboard > h2,
.appointment > h2,
.doctors-details > h2,
.patients-details > h2,
.payment-details > h2 {
  color: #30373e;
  padding-top: 20px;
  font-size: 2rem;
}

.table-title {
  display: flex;
  justify-content: space-between;
}
.patient_list {
  margin-top: 20px;
  background-color: #ffff;
  padding: 20px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
}

.table-title > label {
  font-size: 1rem;
  font-weight: 500;
  text-transform: uppercase;
}
.patient_list > table {
  margin-top: 20px;
  overflow-x: auto;
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  border-top: 1px solid rgb(201, 201, 201);
}

.table_row {
  height: 40px;
}
.table_row > th {
  border-bottom: 1px solid black;
}
.table_row > td {
  text-align: center;
}
tr:nth-child(even) {
  background-color: #f2f2f2;
}

.logout-container {
  display: flex;
  align-items: center;
}
.msg {
  width: 100px;
  background-color: #909090;
  color: white;
  font-size: 12px;
  padding: 5px 5px;
  text-align: center;
  border-radius: 3px;
  display: block;
  margin: 0 8px;
  opacity: 0;
  transition: 0.6s opacity;
}
.logout-container:hover > span {
  opacity: 1;
}
@media screen and (max-width: 1200px) {
  .log_email,
  .password {
    width: 60%;
  }
  .Dr_portal {
    flex-direction: column;
  }
  .dropdown,
  .dropup {
    opacity: 1;
  }
  .table_row > th {
    font-size: 12px;
    border-right: 1px solid black;
    border-left: 1px solid black;
    border-bottom: 1px solid gray;
  }
  .profile {
    flex-direction: column;
  }
  .profile > img {
    margin-top: 20px;
  }
  .profile_info {
    text-align: center;
  }
  .logout-container {
    flex-direction: column;
  }
}

.status {
  display: flex;
}
.new_patients,
.hospital_earn {
  background-color: #ffff;
  padding: 10px 15px;
  display: flex;
  align-items: center;
  color: #30373e;
  border-radius: 5px;
  justify-content: space-between;
  margin: 10px;
}
.new_patients > span,
.hospital_earn > span {
  font-size: 19px;
  font-weight: 500;
}
.status-title {
  display: flex;
  flex-direction: column;
  margin-right: 20px;
}
.status-title > span {
  font-size: 19px;
  font-weight: 500;
}

.status-icon {
  color: #379c94;
  cursor: auto;
}
.charts {
  margin-top: 2rem;
  width: 700px;
  height: 300px;
}
.chartjs-render-monitor {
  height: 350px;
}
@media screen and (max-width: 800px) {
  .status {
    flex-direction: column;
  }
  .charts {
    width: 100%;
  }
  .chartjs-render-monitor {
    width: 100%;
  }
}

.footer {
  position: fixed;
  bottom: -1px;
  display: none;
  width: 100%;
}
.footer_nav {
  display: flex;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #282e33;
  color: white;
  text-align: center;
}
.footer-navlinks {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.footer-navlinks > li {
  text-decoration: none;
  list-style: none;
  font-size: 15px;
}
@media screen and (max-width: 720px) {
  .footer {
    display: block;
  }
  .footer_items {
    font-size: 1.8rem;
    color: #fff;
    padding: 0px 5px;
  }
  .dr-cards {
    padding-bottom: 4rem;
  }
  .dashboard {
    padding-bottom: 5rem;
  }
  .login {
    width: 46px;
    font-weight: 700;
    font-size: 15px;
    color: #fff;
  }
  .nav-items::after {
    content: "";
    display: block;
    width: 0;
    height: 2px;
    background: #fff;
    transition: width 0.3s;
  }
}

